body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wrapper {
  background: linear-gradient(#7b7b7b, #5ccab5);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form-container {
  height: 70vh;
  background: #2c5046;
  padding-top: 100px;
  padding-left: 50px;
  padding-right: 50px;
  margin: auto;
}
input[type="text"] {
  background-color: transparent;
  outline: none;
  border: 0;
  border-bottom: 1px solid #fff;
  width: 100%;
  display: block;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}
button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  background: #000;
  cursor: pointer;
  color: #fff;
  font-weight: 100;
}
.info {
  width: 100%;
  font-size: 20px;
  font-weight: 200px;
  letter-spacing: 2px;
}
.info-key {
  color: #fff;
  border-bottom: 2px solid rgba(255, 255, 255, 0.06);
  font-weight: 400;
}
.info-key:last-child {
  border: 0;
}
.info-value {
  color: #fff;
  font-weight: 200;
}
